.container {
  display: flex;
  flex-direction: column;
  height: 600px;
  padding: 10px 10%;
}
.headContainer {
  height: 20%;
  padding-top: 30px;
  text-align: center;
}
.cardsContainer {
  height: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.buttonContainer {
  height: 30%;
  align-self: center;
}

.BtnChef {
  font-family: "Manrope", sans-serif;
  width: 150px;
  height: 50px;
  border-radius: 30px;
  border: 0;
  color: white;
  font-weight: 500;
  background-color: darkorange;
  cursor: pointer;
}

.cardChef {
  width: 20%;
  transition: 0.3s;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 30px;
  margin-bottom: 20px;
  text-align: center;
}
.cardChef:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}
.cardImgChef {
  width: 100%;
  height: 300px;
  border-radius: 30px 30px 0 0;
  transition: 0.3s;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.cardImgChef:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.cardTitleChef {
  font-family: "Arvo", serif;
  font-weight: bold;
  font-size: 16px;
  color: #474747;
  margin-left: 10px;
  padding-bottom: 10px;
}

@media only screen and (max-width: 728px) {
  .cardsContainer {
    display: grid;
    grid-template-columns: 50% 50%;
  }
  .container {
    height: auto;
  }
  .cardChef {
    width: 80%;
  }
  .headContainer {
    
    margin-bottom: 20px;
  }
}
